"use client";

import { useState } from "react";
import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Image from "next/image";

interface Equipment {
  id: string;
  name: string;
  description: string;
  image: string;
  price: number;
  rentalPrice: number;
  available: number;
}

// Simulated equipment data (in real app, this would come from an API/database)
const equipmentData: Equipment[] = [
  {
    id: "tennis-racket",
    name: "Tennis Racket",
    description: "Professional grade tennis racket",
    image: "https://images.pexels.com/photos/209977/pexels-photo-209977.jpeg?auto=compress&cs=tinysrgb&w=1600",
    price: 150,
    rentalPrice: 15,
    available: 8
  },
  {
    id: "football",
    name: "Football",
    description: "Official size match football",
    image: "https://images.pexels.com/photos/47730/the-ball-stadion-football-the-pitch-47730.jpeg?auto=compress&cs=tinysrgb&w=1600",
    price: 45,
    rentalPrice: 5,
    available: 12
  },
  {
    id: "gym-kit",
    name: "Gym Kit",
    description: "Complete gym kit with resistance bands",
    image: "https://images.pexels.com/photos/4397840/pexels-photo-4397840.jpeg?auto=compress&cs=tinysrgb&w=1600",
    price: 200,
    rentalPrice: 25,
    available: 5
  }
];

export default function StorePage() {
  const { userRole } = useAuth();
  const router = useRouter();
  const [cart, setCart] = useState<{[key: string]: { quantity: number; type: 'buy' | 'rent' }}>({});

  // Protect the store page
  if (!userRole) {
    router.push("/auth/login");
    return null;
  }

  const addToCart = (equipmentId: string, type: 'buy' | 'rent') => {
    setCart(prevCart => {
      const currentItem = prevCart[equipmentId];
      const equipment = equipmentData.find(e => e.id === equipmentId);
      
      if (!equipment || (currentItem?.quantity || 0) >= equipment.available) {
        return prevCart;
      }

      return {
        ...prevCart,
        [equipmentId]: {
          quantity: (currentItem?.quantity || 0) + 1,
          type
        }
      };
    });
  };

  const removeFromCart = (equipmentId: string) => {
    setCart(prevCart => {
      const newCart = { ...prevCart };
      if (newCart[equipmentId].quantity > 1) {
        newCart[equipmentId].quantity -= 1;
      } else {
        delete newCart[equipmentId];
      }
      return newCart;
    });
  };

  const calculateTotal = () => {
    return Object.entries(cart).reduce((total, [equipmentId, item]) => {
      const equipment = equipmentData.find(e => e.id === equipmentId);
      if (!equipment) return total;
      return total + (item.type === 'buy' ? equipment.price : equipment.rentalPrice) * item.quantity;
    }, 0);
  };

  const handleCheckout = async () => {
    // In a real app, this would make an API call to process the order
    alert("Checkout functionality will be implemented with the payment system integration");
  };

  return (
    <div className="min-h-screen bg-black text-white p-8 pt-24">
      <div className="max-w-7xl mx-auto">
        <div className="flex justify-between items-start">
          <h1 className="text-3xl font-bold mb-8 bg-gradient-to-r from-blue-500 to-indigo-600 bg-clip-text text-transparent">
            Equipment Store
          </h1>
          
          {Object.keys(cart).length > 0 && (
            <Card className="bg-zinc-900 border-0 p-4 sticky top-24">
              <h2 className="text-xl font-semibold mb-4 text-blue-400">Cart</h2>
              {Object.entries(cart).map(([equipmentId, item]) => {
                const equipment = equipmentData.find(e => e.id === equipmentId);
                if (!equipment) return null;
                return (
                  <div key={equipmentId} className="flex justify-between items-center mb-2">
                    <div>
                      <p>{equipment.name}</p>
                      <p className="text-sm text-gray-400">
                        {item.type === 'buy' ? 'Purchase' : 'Rental'} x {item.quantity}
                      </p>
                    </div>
                    <div className="flex items-center gap-2">
                      <span>${((item.type === 'buy' ? equipment.price : equipment.rentalPrice) * item.quantity).toFixed(2)}</span>
                      <Button 
                        variant="outline"
                        size="sm"
                        onClick={() => removeFromCart(equipmentId)}
                        className="text-red-400 border-red-400 hover:bg-red-400/10"
                      >
                        Remove
                      </Button>
                    </div>
                  </div>
                );
              })}
              <div className="border-t border-zinc-800 mt-4 pt-4">
                <div className="flex justify-between mb-4">
                  <span>Total:</span>
                  <span>${calculateTotal().toFixed(2)}</span>
                </div>
                <Button 
                  className="w-full bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300"
                  onClick={handleCheckout}
                >
                  Checkout
                </Button>
              </div>
            </Card>
          )}
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {equipmentData.map((item) => (
            <Card key={item.id} className="bg-zinc-900 border-0 overflow-hidden">
              <div className="relative w-full h-48">
                <Image
                  src={item.image}
                  alt={item.name}
                  fill
                  className="object-cover"
                  sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                />
              </div>
              <div className="p-6">
                <h3 className="text-xl font-semibold mb-2 text-blue-400">{item.name}</h3>
                <p className="text-gray-400 mb-4">{item.description}</p>
                <div className="flex justify-between items-center mb-2">
                  <span>Purchase Price:</span>
                  <span>${item.price}</span>
                </div>
                <div className="flex justify-between items-center mb-4">
                  <span>Rental Price:</span>
                  <span>${item.rentalPrice}/day</span>
                </div>
                <div className="flex justify-between items-center mb-4">
                  <span>Available:</span>
                  <span>{item.available - (cart[item.id]?.quantity || 0)}</span>
                </div>
                <div className="grid grid-cols-2 gap-2">
                  <Button
                    variant="outline"
                    onClick={() => addToCart(item.id, 'rent')}
                    disabled={item.available <= (cart[item.id]?.quantity || 0)}
                    className="w-full border-blue-500 text-blue-500 hover:bg-blue-500/10"
                  >
                    Rent
                  </Button>
                  <Button
                    onClick={() => addToCart(item.id, 'buy')}
                    disabled={item.available <= (cart[item.id]?.quantity || 0)}
                    className="w-full bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300"
                  >
                    Buy
                  </Button>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}
